@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400&display=swap);
html {
    --color0: #FFD951;
    --color1: #32DAFF;
    --color2: #3BF384;
    --color3: #F19FFF;
    --color4: #AE92FF;
    --color5: #FFFFFF;
    --color6: #9C9C9C;
    --color7: #FF9900;
    --color8: #69009B;
    --color9: #D46A83;
}

.color0 {
    background: #FFD951;
    background: var(--color0);
}

.color1 {
    background: #32DAFF;
    background: var(--color1);
}

.color2 {
    background: #3BF384;
    background: var(--color2);
}

.color3 {
    background: #F19FFF;
    background: var(--color3);
}

.color4 {
    background: #AE92FF;
    background: var(--color4);
}

.color5 {
    background: #FFFFFF;
    background: var(--color5);
}

.color6 {
    background: #9C9C9C;
    background: var(--color6);
}

.color7 {
    background: #FF9900;
    background: var(--color7);
}

.color8 {
    background: #69009B;
    background: var(--color8);
}

.color9 {
    background: #D46A83;
    background: var(--color9);
}
.item {
    display: flex;
    justify-content: space-between;
    margin: 0 0 2rem 0;
}

.item input {
    height: 5rem;
    font-family: var(--main-font);
    font-weight: 400;
    font-size: 3rem;
    line-height: 5rem;
    padding: 0;
    background: var(--high-color);
    border: var(--main-border);
}

.item .text {
    width: calc(40rem - 35px);
    height: 5rem;
    font-size: 3rem;
    line-height: 5rem;
    text-align: left;
    padding: 0 0 0 5px;
    background: var(--high-color);
    border: var(--main-border);
}

.item .number {
    width: 5rem;
    border-radius: 100%;
    -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

button.delete {
    transform: rotate(-45deg);
}

.palette {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    background: var(--line-color);
    padding: 0.5rem;
}

.palette-square {
    width: 10.5rem;
    height: 10.5rem;
    margin: 0.5rem auto;
    border: var(--main-border);
}

.items-list {
    overflow-y: scroll;
    width: 100%;
    max-height: 25vh;
    font-size: 2rem;
    border: var(--main-border);
    background: var(--high-color);
}

.items-list-item {
    width: 100%;
    height: 3rem;
    padding: 1vh 0 2.5vh 0;
    font-size: 2rem;
    border: 0;
    border-bottom: var(--main-border);
    border-radius: 0;
}

.items-list-item:last-child {
    border-bottom: 0;
}

.week-number-arrow-up {
    background: rgba(255, 0, 0, 0.4) url(/scida/static/media/arrowUp.db38afd6.svg) center/cover;
}

.week-number-arrow-down {
    background: rgba(255, 0, 0, 0.4) url(/scida/static/media/arrowDown.2182d902.svg) center/cover;
}

header {
    text-align: center;
}

.logo {
    margin: 1rem;
    width: 12rem;
}

nav {
    display: flex;
    justify-content: space-evenly;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 50px;
    max-height: 200px;
    background: var(--high-color);
    border-top: var(--main-border);
}

a {
    color: var(--line-color);
    text-decoration: none;
}

.menu-item {
    width: 25vw;
    height: auto;
    border: none;
}

.menu-icon {
    width: 5rem;
    height: 5rem;
    font-size: 5rem;
    font-weight: 300;
    border-radius: 100%;
}

.menu-icon span {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    border: var(--main-border);
    border-radius: 100%;
}

.menu-text {
    font-size: 1.5rem;
    line-height: 5rem;
    text-transform: uppercase;
}

.here {
    color: #c2c2c2;
}

.menu-item.here .menu-icon span {
    border-color: #c2c2c2;
}

.menu-item.here .menu-icon span.done {
    background: #c2c2c2;
}
h1 {
  margin: 2rem;
  font-size: 4rem;
  font-weight: 400;
  text-transform: uppercase;
}

h2 {
  margin: 3rem;
  font-size: 3rem;
  font-weight: 300;
  text-transform: uppercase;
}

main {
  margin: 0 0 12vh 0;
}

button {
  width: 5rem;
  height: 5rem;
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 3rem;
  padding: 0;
  background: var(--high-color);
  border: var(--main-border);
  border-radius: 100%;
}

button.plus-sign {
  font-size: 5rem;
  font-weight: 300;
  line-height: 0;
}

button.delete {
  transform: rotate(-45deg);
}

.done {
  background: var(--line-color);
}

.edit-box {
  width: 97vw;
  margin: 0 auto 2rem auto;
  background: var(--base-color);
}

.edit-box-buttons {
  height: 7rem;
}

button.edit-complete {
  float: right;
  margin: 1rem;
}

table {
  width: 100%;
  margin: 0 0 2rem 0;
  border-collapse: collapse;
}

tr {
  height: 5rem;
}

td {
  font-size: 3rem;
  border: var(--main-border);
}

.week {
  margin: 0 auto 5vh auto;
}

.week-date {
  border: none;
}

.week-date div {
  height: 4rem;
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 2.5rem;
  line-height: 4rem;
  padding: 0;
  background: var(--base-color);
  border: none;
}

.day {
  width: 5rem;
  background: var(--high-color);
}

.left-column {
  text-align: left;
  padding: 0 0 0 10px;
}

.week-item-number {
  width: 5rem;
}

.week-spots {
  background: rgba(255,255,255,0.5);
}

.allDone, .allDone .week-spots {
  color: var(--high-color);
  background: var(--line-color);
}

.spot {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
}

.spot.open {
  border: var(--main-border);
  background: var(--high-color);
}

.spot.closed {
  border: var(--main-border);
  background: var(--line-color);
}

.allDone .spot.closed {
  border: #1dff75;
  background: #1dff75;
}

.scooch {
  box-shadow: -2px -2px 2px 0 var(--line-color);
  transform: translate(2px, 2px);
  transition: all ease-in-out;
}

* {
  --base-color: #efefef;
  --high-color: #ffffff;
  --line-color: #000000;
  --main-font: 'Josefin Sans', sans-serif;
  --main-border: 2px solid #000000;
  font-size: 1.6vw;
  font-family: var(--main-font);
  text-align: center;
  box-sizing: border-box;
  margin: auto;
}

body {
  background: var(--base-color) url(/scida/static/media/tuttledotG.2b514dd6.svg) center 20vh/150vw no-repeat;
}

